// WordPress Generated Classes
// http://codex.wordpress.org/CSS#WordPress_Generated_Classes
$spacer: 40px;

// Media alignment
.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}
.aligncenter {
  display: block;
  margin: ($spacer / 2) auto;
  height: auto;
}
.alignleft,
.alignright {
  margin-bottom: ($spacer / 2);
  height: auto;
}
@media #{$medium-and-up} {
  // Only float if not on an extra small device
  .alignleft {
    float: left;
    max-width: 50%;
    margin-right: ($spacer / 2);
  }
  .alignright {
    float: right;
    max-width: 50%;
    margin-left: ($spacer / 2);
  }
}

